import axios from "axios";
import { useSelector ,useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import styled from "styled-components";

import DeleteIcon from '@mui/icons-material/Delete';
import { async } from "@firebase/util";

const Container = styled.div`
  display: flex;
  gap: 10px;
  margin: 30px 2px;
  border-top: 1px solid gray;
`;

const Avatar = styled.img`
  width: 40px;
  height: 40px;
  border-radius: 50%;
`;

const Details = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: ${({ theme }) => theme.text};
`;
const Name = styled.span`
  font-size: 13px;
  font-weight: 500;
`;

const Date = styled.span`
  font-size: 12px;
  font-weight: 400;
  color: ${({ theme }) => theme.textSoft};
  margin-left: 5px;
`;

const Text = styled.span`
  font-size: 14px;
`;

const Comment = ({ comment, handleDelete }) => {
  const { currentUser } = useSelector((state) => state.user);


  const [channel, setChannel] = useState({});
  const [CommentText, setCommentText] = useState(comment.desc);

  useEffect(() => {
    const fetchComment = async () => {
      const res = await axios.get(`https://random-ochre.vercel.app/api/users/find/${comment?.userId}`);
      setChannel(res.data)
    };
    fetchComment();
  }, [comment.userId]);

  
  return (
    <Container>
      <Avatar src={channel.img} />
      <Details>
        <Name>
          {channel.dispName} <Date>1 day ago</Date> 
          { comment.userId ==  currentUser?._id ?  < DeleteIcon onClick ={()=> handleDelete(comment)} /> : null}
        </Name>
        <Text>{CommentText}</Text>
      </Details>
       
    </Container>
  );
};

export default Comment;
