import { async } from "@firebase/util";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import Comment from "./Comment";

const Container = styled.div``;

const NewComment = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const Avatar = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 50%;
`;

const Input = styled.input`
  border: none;
  border-bottom: 1px solid ${({ theme }) => theme.soft};
  color: ${({ theme }) => theme.text};
  background-color: transparent;
  outline: none;
  padding: 5px;
  width: 100%;
`;

const Comments = ({videoId}) => {

  const { currentUser } = useSelector((state) => state.user);

  const [comments, setComments] = useState([]);
  const [comment,setcomment] = useState("");
  const handleDelete = async(comment) =>{
    
    await axios.delete(`https://random-ochre.vercel.app/api/comments/${comment._id}`);
    setComments(comments.filter((c)=>{
      return c._id!=comment._id;
    }))
      

  }
  useEffect(() => {
    const fetchComments = async () => {
      try {
        const res = await axios.get(`https://random-ochre.vercel.app/api/comments/${videoId}`);
        setComments(res.data);
      } catch (err) {}
    };
    fetchComments();
  }, [videoId]);

const submitComment = async(event)=>
{
  event.preventDefault();

  if(comment!="")
  {
    const response  = await axios.post("https://random-ochre.vercel.app/api/comments",{desc:comment, videoId});
    // console.log(response);
    setComments([...comments,response?.data])
  }
}

function handleChange(event) {
  

  setcomment(event.target.value)

}


  return (
    <Container>
        
      {currentUser? 
      <NewComment>
        <Avatar src={currentUser?.img} />
        <Input placeholder="Type your comment here" onChange={handleChange}/>
      <button style={{ backgroundColor:"#cc1a00" , fontWeight:"400", color:"white", borderRadius:"5px", padding:"10px" }}  onClick={submitComment}>Submit</button>
      </NewComment>
      
      :null}

  
      {comments.map(comment=>(
        <Comment key={comment._id} comment={comment}  handleDelete={handleDelete}/>
      ))}
    </Container>
  );
};

export default Comments;
